@import url(https://fonts.googleapis.com/css2?family=Inter:wght@400;700&display=swap);

:root {
    --darkbg: hsl(230, 17%, 14%);
    --darkheader: hsl(232, 19%, 15%);
    --lightbg: hsl(0, 0%, 100%);
    --lightheader: hsl(225, 100%, 98%);
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: "Inter", sans-serif;
}

.app {
    background-color: hsl(230, 17%, 14%);
    background-color: var(--darkbg);
    width: 100vw;
    height: 100%;
    min-height: 100vh;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    transition: all 0.3s ease;
    overflow-y: auto;
}

.content {
    width: 1200px;
    max-width: 80%;
    height: 90vh;
    z-index: 99;
}

.socialItem {
    background-color: blue;
}

.hero {
    background-color: hsl(232, 19%, 15%);
    width: 100%;
    height: 250px;
    position: absolute;
    top: 0;
    left: 0;
    border-bottom-left-radius: 20px;
    border-bottom-right-radius: 20px;
    transition: all 0.3s ease;
}

.lightmode {
    background-color: hsl(0, 0%, 100%);
    background-color: var(--lightbg);
    color: hsl(230, 17%, 14%);
}

.lightmodeHero {
    background-color: hsl(225, 100%, 98%);
    background-color: var(--lightheader);
}
.followersList {
    height: 200px;
    width: 100%;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-column-gap: 1.5rem;
    -webkit-column-gap: 1.5rem;
            column-gap: 1.5rem;
    margin-top: 4rem;
}

@media (max-width: 780px) {
    .followersList {
        grid-template-columns: repeat(2, 1fr);
        grid-row-gap: 1.5rem;
        row-gap: 1.5rem;
        height: 400px;
    }
}

@media (max-width: 530px) {
    .followersList {
        grid-template-columns: 1fr;
        grid-row-gap: 1.5rem;
        row-gap: 1.5rem;
        height: auto;
    }
}
.followersGained {
    background-color: hsl(228, 28%, 20%);
    border-radius: 5px;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    align-items: center;
    transition: all 0.1s ease;
}

.FGLight {
    background-color: hsl(227, 47%, 96%);
}

.followersGained:hover {
    background-color: hsl(228, 28%, 30%);
    cursor: pointer;
}

.FGLight:hover {
    background-color: hsl(227, 47%, 90%);
}

.color-line {
    height: 5px;
    width: 100%;
}

.socialMedia {
    display: flex;
    justify-content: center;
    padding-top: 1rem;
}

.socialMedia>img {
    padding-right: 0.5rem;
}

.socialMedia>p {
    color: hsl(228, 34%, 66%);
    font-weight: 600;
    font-size: 12px;
}

.followersCount {
    text-align: center;
}

.followersCount>h1 {
    font-size: 42px;
}

.followersCount>p {
    font-size: 12px;
    color: hsl(228, 34%, 66%);
}

.todayGained {
    display: flex;
}

.todayGained {
    color: hsl(163, 72%, 41%);
    font-weight: 600;
}

.followersGained__content {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}

@media (max-width: 530px) {
    .followersGained {
        padding-bottom: 1.2rem;
    }
    .followersCount {
        margin-top: 1.5rem;
    }
    .todayGained {
        margin-top: 1.5rem;
    }
}
.overview {
    margin-top: 3rem;
}

.overview__content {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-column-gap: 1.5rem;
    -webkit-column-gap: 1.5rem;
            column-gap: 1.5rem;
    grid-row-gap: 1.5rem;
    row-gap: 1.5rem;
    grid-template-rows: 1fr 1fr;
    border-radius: 5px;
    margin-top: 20px;
}

@media (max-width: 1040px) {
    .overview__content {
        grid-template-columns: repeat(3, 1fr);
    }
}

@media (max-width: 780px) {
    .overview__content {
        grid-template-columns: repeat(2, 1fr);
    }
}

@media (max-width: 530px) {
    .overview__content {
        grid-template-columns: 1fr;
        grid-row-gap: 1.5rem;
        row-gap: 1.5rem;
    }
}
.overview {
    width: 100%;
}

.overviewItem {
    background-color: hsl(228, 28%, 20%);
    height: 140px;
    padding: 1.5rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    transition: all 0.1s ease;
}

.overviewDark {
    background-color: hsl(227, 47%, 96%);
}

.overviewItem:hover {
    background-color: hsl(228, 28%, 30%);
    cursor: pointer;
}

.overviewDark:hover {
    background-color: hsl(227, 47%, 90%);
}

.overviewItem__top>p {
    color: hsl(228, 34%, 66%);
    font-weight: 600;
}

.overviewItem__top {
    display: flex;
    justify-content: space-between;
}

.overview__percentage {
    display: flex;
    color: hsl(163, 72%, 41%);
    font-weight: 600;
}

.negative {
    color: hsl(356, 69%, 56%);
}

.overviewItem__bottom {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.overviewItem__bottom>h2 {
    font-size: 2rem;
}
.header {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.header__info>p {
    color: hsl(228, 34%, 66%);
    font-weight: 600;
}

.header__darkmode {
    display: flex;
    align-items: center;
}

@media (max-width: 430px) {
    .header {
        flex-direction: column;
    }
    .header__darkmode {
        margin-top: 0.5rem;
        width: 100%;
        justify-content: space-between;
    }
    .header__info {
        width: 100%;
    }
    .header__info>h1 {
        font-size: 24px;
    }
}
